'use client'
import styles from "../../page.module.css";
import Form from 'react-bootstrap/Form';
import EntryBox from "../../_components/entrybox";
import Submit from "../../_components/submit";
import Select from "../../_components/select";
import { useEffect, useReducer } from "react";
import CheckBox from "../../_components/checkbox";
import { fetch } from "../../_scripts/communications";
import backgroundImage from "../../../public/Green.png"
import ButtonEntry from "@/app/_components/buttonentry";
import FormTitle from "@/app/_components/formtitle";
import Feedback from "@/app/_components/feedback";

let submitObject = {};

const initState = {
    'Load Account':{
        value:''
    },
    'Select Student':{
        options:[],
        value:''
    },
    'LCS Account Number': {
        value: ''
    },
    'First Name':{
        value:''
    },
    'Last Name':{
        value:''
    },
    'Email':{
        value:''
    },
    'Phone':{
        value:''
    },
    'Submit':{
        Color:'primary',
        Text:'Submit'
    },
    'Feedback':{
        color:'success',
        code:'',
        title:'',
        text:'',
        hidden: true
    }
};


const reducer = (state, action)=>{

    let newState = {...state};

    switch (action.type){

        case 'update-Value':{

            newState[action.label].value = action.value;

            if(action.label=='Select Student'){

                let selectedStudent = newState['Select Student'].options.find(x=>x.ID==newState['Select Student'].value);

                newState['First Name'].value = selectedStudent.First_Name;
                newState['Last Name'].value = selectedStudent.Last_Name;
                newState['Email'].value = selectedStudent.Email;
                newState['Phone'].value = selectedStudent.Phone;
                newState['LCS Account Number'].value = selectedStudent.LSManager_Account;
            }

            newState.Submit.Color = 'primary';
            newState.Submit.Text = 'Submit'

            return newState

            break
        }

        case 'submit-Clicked':{

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Error':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;
        }

        case 'submit-Success':{

            //Seperate from click for future purposes

            newState.Submit.Color = action.color;
            newState.Submit.Text = action.text;

            return newState

            break;

        }

        case 'load-Options':{

            //console.dir(action.data);

            let sessions = action.data.data.sessions;

            let students = action.data.data.students;

            newState['Select Student'].options = students;
            
            newState['Select Student'].value = newState['Select Student'].options[0].ID;

            return newState

            break;
        }
        case 'load-Feedback':{

            newState['Feedback'].hidden = false;
            newState['Feedback'].color = action.color;
            newState['Feedback'].code = action.code;
            newState['Feedback'].title = action.title;
            newState['Feedback'].text = action.text;

            return newState;

            break;

        }


    }
};

export default function UpdateStudent(){


    const [state, dispatch] = useReducer(reducer,initState);


        submitObject = {
            ID: state['Select Student'].value,
            First_Name:state['First Name'].value,
            Last_Name: state['Last Name'].value,
            Email:state['Email'].value,
            Phone:state['Phone'].value,
            LSManager_Account:state["LCS Account Number"].value
        }
        

    return (

        <main className={styles.main} style={{backgroundImage: `url(${backgroundImage.src})`}}>
            
          <Form>
            <FormTitle text='Update Student'></FormTitle>
            <ButtonEntry
                label="Load Account"
                type="numeric"
                placeholder="LS Manager Account"
                value={state['Load Account'].value}
                dispatch={dispatch}
                data={{'LSManager_Account':state['Load Account'].value}}
                endpoint="LoadStudentsAndSessions"
            >
            </ButtonEntry>
            <Select
                options={state['Select Student'].options}
                label='Select Student'
                value={state['Select Student'].value}
                dispatch={dispatch}
            >
            </Select>
            <EntryBox
                label='LCS Account Number'
                type = 'numeric'
                reminder ='Please Enter for LSManager'
                placeholder = ''
                value = {state['LCS Account Number'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='First Name'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['First Name'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Last Name'
                type = 'text'
                reminder =''
                placeholder = ''
                value = {state['Last Name'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Email'
                type = 'email'
                reminder =''
                placeholder = ''
                value = {state['Email'].value}
                dispatch={dispatch}
            ></EntryBox>
            <EntryBox
                label='Phone'
                type = 'tel'
                reminder =''
                placeholder = ''
                value = {state['Phone'].value}
                dispatch={dispatch}
            ></EntryBox>
            <Submit 
                method="POST"
                endpoint="UpdateStudent"
                dispatch={dispatch}
                data={submitObject}
                color={state.Submit.Color}
                text={state.Submit.Text}
            ></Submit>
            <Feedback
                color={state['Feedback'].color}
                code={state['Feedback'].code}
                title={state['Feedback'].title}
                text={state['Feedback'].text}
                hidden={state['Feedback'].hidden}
            ></Feedback>
          </Form>
        </main>
    );
}