'use client'
import '../../public/css/bootstrap.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import { fetch } from "../_scripts/communications";


function ButtonEntry(p) {

    const {label,type='email',reminder, placeholder, value,dispatch,hidden=false,endpoint,data} = p;

  return (
      <Form.Group hidden={hidden} className="mb-3" controlId={`form${label}`}>
        <InputGroup className="mb-3">
            <Button 
            onClick={async()=>{
              let response = await fetch({method:"POST",endpoint:endpoint,data:data});
              console.dir(response);
              dispatch({type:'load-Options',data:response})
            }}
            variant="secondary" id="button-addon1">
            Load
            </Button>
            <Form.Control
            type={type}
            placeholder={placeholder}
            value={value}
            onChange={e=>dispatch({type:'update-Value',value:e.target.value, label:label})}
            aria-label="Example text with button addon"
            aria-describedby="basic-addon1"
            autoComplete='off'
            />
        </InputGroup>
      </Form.Group>
  );
}

export default ButtonEntry;